* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

body {
  background: black;
}

.App-Background {
  background: black;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* Scrollbar.css */

/* Track */
::-webkit-scrollbar {
  width: 5px;
  
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #4e5e8059;
  border-radius: 12px;
  height: 5px;
 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #4e5e80;
}

/* Track when the content is being scrolled */
::-webkit-scrollbar-track {
  background: none;
}

/* Add a slight border around the scrollbar */
::-webkit-scrollbar-track-piece {
  border: 0px solid #2b2b2b4c;
  height: 5px;
}


.gradient_blue1 {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    45deg,
    #000000 50%,
    #1b037b5f 100%
  );
  mix-blend-mode: difference;
  z-index: 2;
}

.gradient_blue2 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    225deg,
    #000000 50%,
    #1b037b5f 100%
  );
  z-index: 1; /* Place it above the blue background */
}

a {
  color: unset;
  text-decoration: none;
}

.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section__padding {
  padding: 4rem 6rem;
}

.section__margin {
  margin: 4rem 6rem;
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1)
    both;
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@media screen and (max-width: 768px) {
  .section__padding {
    padding: 4rem;
  }

  .section__margin {
    margin: 4rem;
  }
}

@media screen and (max-width: 550px) {
  .section__padding {
    padding: 4rem 2rem;
  }

  .section__margin {
    margin: 4rem 2rem;
  }
}
